import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		default:
			break;
	}
}

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { userStore } = this.props.stores;
		const { user, team, dogs, workouts, workoutSummary } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 mb-4'>
						{/* <h5 class='mt-4'>user</h5>
						{JSON.stringify(user)} */}
						{/* <h5 class='mt-4'>team</h5>
						{JSON.stringify(team)} */}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<div class='col-12 col-sm-6 col-lg-2 mt-2 d-flex'>
									{/* {JSON.stringify(dog, null, 4)} */}
									<div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
										{!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3 pr-2'
										style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
									>
										<span class='float-right'>
											{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
											{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
										</span>
										<a class='stretched-link' href={`/dogs/${dog.id}`}>
											<h5>{dog.shortname || dog.name}</h5>
										</a>
										{userStore.findTeam(dog.team)}<br />
										<small>
											{dog.chipdId && <small>{dog.chipId}</small>}
										</small>
									</div>
								</div>
							))}
						</div>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>				
						<h5>Treningsturene - {workouts.length} stk</h5>
						<div class='row'>
							{workouts && workouts.map(workout => (
								<div class='col-12 col-sm-6 col-lg-2 mt-2 p-2 d-flex'>
									{/* {JSON.stringify(workout, null, 4)} */}
									<div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`} style={workout.image ? `background-image: url("${workout.image.s3SmallLink}");` : ''}>
										{!workout.image && <i class={`${getWorkoutIcon(workout)} text-muted mt-3`} style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3'>
										<a class='stretched-link' href={`/workouts/${workout.id}`}>
											<h5 class='my-0'>{workout.name}</h5>
										</a>
										{userStore.findTeam(workout.team)}<br />
										<small>
											<span class='text-muted'>
												<i class='fas fa-tools' /> {workout.equipment}
												<i class='fas fa-road ml-2' /> {workout.distanceKm}km
												<i class='fas fa-mountain ml-2' /> {workout.elevation}m
												{workout.dogs && <span><i class='fas fa-dog ml-2' /> {workout.dogs.length}</span>}
											</span>
											<i>{workout.url}</i><br />
										</small>
										<small class='text-muted'>
											{util.isoDate(workout.date, false, false, true)}
										</small>
									</div>
								</div>
							))}
						</div>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>	
						<h5 class='mt-4'>Treningsoversikt</h5>
						{/* {JSON.stringify(workoutSummary)} */}
						{workoutSummary && workoutSummary.map(summary => (
							<div class='col-12 col-sm-12 col-lg-2 mt-2 p-2 d-flex'>
								{/* {JSON.stringify(workout, null, 4)} */}
								<div class={`text-center border rounded-circle imageRounded`}>
									<span class={`text-muted mt-4`} style='font-size: 40px;'>{summary._id.week}</span>
								</div>
								<div class='flex-grow-1 pl-3'>
									<h5 class='my-0'>{summary._id.week} - {summary._id.year}</h5>
									<span class='text-muted'>
										<i class='fas fa-stopwatch-20' /> {summary.count}
										<i class='fas fa-road ml-2' /> {summary.distanceKm}km
										<i class='fas fa-mountain ml-2' /> {summary.elevation}m
										<i class='fas fa-stopwatch ml-2' /> {util.secToHms(summary.duration)}
										<i class='fas fa-tachometer-alt ml-2' /> {summary.speedAvg}km/t
									</span>
								</div>
							</div>
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
